<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

//paddings and margins
$pd-desktop: 0 130px;
$mg-desktop: 0 40px;

//circle
$circle: 50%;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

.servicos-wrapper {
  height: auto;
  background-color: $white;

  @media only screen and (max-width: 1919px) {
    height: auto;
  }

  .servicos {
    // margin-bottom: pxToEm(70);
    display: flex;
    height: 100%;
    justify-content: space-between;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
    }

    .servico {
      padding: pxToEm(60) pxToEm(80);
      width: 50%;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: space-between;

      @media only screen and (min-width: 1200px) and (max-width: 1919px) {
        padding: pxToEm(40) pxToEm(60);
      }

      @media only screen and (min-width: 992px) and (max-width: 1199px) {
        padding: pxToEm(40) pxToEm(60);
      }

      @media only screen and (max-width: 991px) {
        width: auto;
        padding: pxToEm(40);
        align-items: center;
      }

      @media only screen and (max-width: 599px) {
        text-align: center;
        align-items: center;
      }

      &:nth-child(2) {
        background-color: $turquiose;

        .txt-big {
          color: $white;
          font-family: "OS-Bold";
        }

        .line {
          background-color: $white;
        }

        .copy {
          color: $white;
          font-family: "OS-Regular";
        }

        .topics {
          color: $white;

          .topic {
            color: $white;
          }
        }
      }

      .txt-big {
        font-size: pxToEm(28);
        color: $dk-gray-3;
        font-family: "OS-Bold";
        margin-bottom: pxToEm(15);

        @media only screen and (max-width: 1919px) {
          margin-bottom: pxToEm(20);
        }
      }

      .line {
        height: pxToEm(1);
        width: pxToEm(80);
        background-color: $turquiose;
        margin-bottom: pxToEm(20);

        @media only screen and (max-width: 1919px) {
          margin-bottom: pxToEm(20);
        }
      }

      .copy {
        color: $dk-gray-3;
        width: 100%;
        font-size: pxToEm(22);
        font-family: "OS-Regular";
        margin-bottom: pxToEm(20);

        @media only screen and (max-width: 1919px) {
          margin-bottom: pxToEm(20);
        }

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }

      .topics {
        display: flex;
        flex-direction: column;
        font-family: "OS-Regular";
        font-size: pxToEm(20);

        @media only screen and (max-width: 991px) {
          text-align: center;
        }
      }
    }
  }
}
</style>



